import React from "react"
import Layout from "../components/Layout"
import "../styles/home.css"
import Reel from "../videos/BBI_15SecondReel.mp4"
import Container from "@material-ui/core/Container"
import { Grid } from "@material-ui/core"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import Car from "../images/car.webp"
import Serv from "../images/ServicesImage.webp"
import Team from "../images/team.webp"
import bg from "../images/comb1.webp"
import { Helmet } from "react-helmet"

export default function Home() {
  const data = useStaticQuery(graphql`
    query homePage {
      food: file(relativePath: { eq: "foodpantry.png" }) {
        childImageSharp {
          fluid(quality: 100) {
            ...GatsbyImageSharpFluid_withWebp_noBase64
          }
        }
      }
      nwc: file(relativePath: { eq: "nationwidechildrens.png" }) {
        childImageSharp {
          fluid(quality: 100) {
            ...GatsbyImageSharpFluid_withWebp_noBase64
          }
        }
      }
      hum: file(relativePath: { eq: "humane.png" }) {
        childImageSharp {
          fluid(quality: 100) {
            ...GatsbyImageSharpFluid_withWebp_noBase64
          }
        }
      }
      mash: file(relativePath: { eq: "Mash.png" }) {
        childImageSharp {
          fluid(quality: 100) {
            ...GatsbyImageSharpFluid_withWebp_noBase64
          }
        }
      }
    }
  `)
  return (
    <Layout>
      <Helmet>
        <html lang="en" />
        <title>BBI Logistics Home Page</title>
        <meta name="description" content="This is the home page of BBI Logistics. We are on the forefront of freight and we will get  the job done fast and efficient. " />
        
      </Helmet>
      <div className="pop">
        <div className="videoPlay">
          <video
            class="overlay"
            autoPlay
            loop
            muted
            playsInline
            src={Reel}
            type="video/mp4"
          />
        </div>
        <div className="overlay">
          <h2>BUILT ON BETTER INTEGRITY</h2>
        </div>
      </div>

      <Container fluid className="acc cushion">
      <div class="wrapper">
   <div class="card" style={{ backgroundImage: `url(${Car})` }}>
   <h3>SHIPPERS</h3>
      <div class="text-wrapper t1">
        
         <div class="text">
         SHIPPERS
         </div>
         <div class="text-small">
         Leverage our vast carrier network to improve efficiency in your
            supply chain. We tailor our solutions around your needs to guarantee
            the most reliable transportation in the industry. The ability to
            adapt and scale our team around your needs allows us to grow
            together.
         </div>
      </div>
   </div>
   <div class="card" style={{ backgroundImage: `url(${Serv})` }}>
   <h3>CARRIERS</h3>
         <div class="text-wrapper t2">
            <div class="text">
            CARRIERS
              </div>
            <div class="text-small">
            Utilize our dedicated freight to help maximize your load potential.
            We can provide you with reliable backhauls, long miles, local runs,
            and expedited shipments. Our 24/7/365 support and technology allow
            for easy updates and hassle-free billing.
         </div>
      </div>
   </div>
   <div class="card" style={{ backgroundImage: `url(${Team})` }}>
   <h3>EMPLOYEMENT OPPURTUNITY</h3>
         <div class="text-wrapper t3">
            <div class="text">
            EMPLOYEMENT OPPURTUNITY
            </div>
            <div class="text-small">
               Lorem ipsum dolor sit amet, consectetur adipiscing elit. Curabitur pellentesque metus quam. Cras finibus quam eget metus tristique, eu lobortis libero auctor. Quisque sollicitudin in lectus sit amet tincidunt.  
         </div>
      </div>
   </div>
</div>

      </Container>

      <Container maxWidth={false} className="CC ">
        <Container>
        <Grid className="cred" container>
          <Grid className="center" item xs={12} sm={6}>
            <h7>
              <span className="blue"> BUILT ON BETTER INTRGRITY</span>
              <span>
                , a company that customers endorse, carriers prefer, and
                employees are proud of. We go the{" "}
                <span className="lblue">EXTRA MILE</span> to ensure exceptional
                transportation solutions and scalability.
              </span>
            </h7>
            <Grid container >
              <Grid className="cred" item xs={6} sm={6}>
                <div className="center">
                  <h3>25,000+</h3>
                  <p>Carrier Network</p>
                </div>
              </Grid>
              <Grid className="cred" item xs={6} sm={6}>
                <div className="center">
                  <h3>800+</h3>
                  <p>Clients Served</p>
                </div>
              </Grid>

              <Grid item xs={6} sm={6}>
                <div className="center">
                  <h3>4</h3>
                  <p> 100%+ YOY Growth </p>
                </div>
              </Grid>
              <Grid item xs={6} sm={6}>
                <div className="center">
                  <h3>4</h3>
                  <p>Years of Service</p>
                </div>
              </Grid>
            </Grid>
          </Grid>
          <Grid className="center" item xs={12} sm={6}>
            <Img fluid={data.mash.childImageSharp.fluid} alt="combination of images " />
          </Grid>
        </Grid>
        </Container>
      </Container>
      <Container maxWidth={false} className=" ">
      <Container  style={{ backgroundImage: `url(${bg})` }} className="phil ">
        <h1>PHILANTHROPIC</h1>
        <h2>"We give back to the communities in which we work"</h2>
        <h3>Proud supporters of:</h3>
        <Grid className="cred" container spacing={4}>
          <Grid item xs={8} sm={3}>
            <div>
              <Img fluid={data.hum.childImageSharp.fluid} alt="humane society logo columbus" />
            </div>
          </Grid>
          <Grid item xs={8} sm={3}>
            <div>
              <Img fluid={data.nwc.childImageSharp.fluid} alt="nationwide childrens columbus logo" />
            </div>
          </Grid>
          <Grid item xs={8} sm={3}>
            <div>
              <Img fluid={data.nwc.childImageSharp.fluid} alt="nationwide childrens columbus logo" />
            </div>
          </Grid>
          <Grid item xs={8} sm={3}>
            <div>
              <Img fluid={data.food.childImageSharp.fluid} alt="worthington resource pantry logo" />
            </div>
          </Grid>
        </Grid>
      </Container>
      </Container>
    </Layout>
  )
}
